.benefits {
  background-color: #e3ecf6;
  background-image: url("../img/bg-benefits-desktop.jpg");
  background-repeat: no-repeat;
  background-position: 0 center;
  background-size: cover;
}
@media (max-resolution: 144dpi), (max-resolution: 1.5dppx) {
  .benefits {
    background-image: url("../img/bg-benefits-desktop@2x.jpg");
    background-repeat: no-repeat;
    background-position: 0 center;
    background-size: cover;
  }
}

.benefits__wrapper {
  width: 1160px;
  margin: 0 auto;
  box-sizing: content-box;
  padding-top: 110px;
}
