@media (max-width: 1023px) {
  .benefits__wrapper h2 {
    font-size: 26px;
    line-height: 39px;
  }
}
@media (max-width: 767px) {
  .benefits__wrapper h2 {
    font-size: 24px;
    line-height: 36px;
  }
}

.benefits__list {
  flex-wrap: wrap;
  margin: 0 auto;
  display: grid;
  gap: 40px;
  grid-template-columns: repeat(3, 1fr);
  padding-top: 50px;
  padding-bottom: 114px;
}
@media (min-width: 1024px) and (max-width: 1365px) {
  .benefits__list {
    gap: 0;
  }
}
@media (max-width: 1023px) {
