.benefits__item {
  position: relative;
  padding: 30px 42px 50px 30px;
  background-color: #f9fbfd;
  width: 360px;
  min-height: 241px;
}
.benefits__item--zodiac {
  padding: 30px 34px 50px 30px;
}
.benefits__item--allinclude {
  padding: 30px 50px 50px 30px;
}
@media (min-width: 1024px) and (max-width: 1365px) {
  .benefits__item {
    padding: 30px 20px 20px 20px;
    width: 95%;
    height: auto;
  }
}
@media (max-width: 1023px) {
  .benefits__item {
    min-width: 206px;
    width: auto;
    min-height: 310px;
    padding: 26px 15px 21px 19px;
  }
}
@media (max-width: 767px) {
  .benefits__item {
    min-height: 249px;
    padding: 26px 24px 21px 20px;
  }
}

.benefits__item p {
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #011c40;
  margin: 0;
}

.benefits__item-title {
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
