  }
  .benefits__item-title--zodiac {
    margin: 43px 0 20px;
  }
}
@media (max-width: 767px) {
  .benefits__item-title {
    width: 190px;
  }
  .benefits__item-title--zodiac {
    margin: 45px 0 8px;
  }
}

.benefits__item::before {
  content: "";
  top: 10px;
  background-color: transparent;
  width: 80px;
  height: 80px;
  position: absolute;
  left: 118px;
}

.benefits__item--liner::before {
  left: 5px;
  top: 5px;
  background-image: url("../img/svg/icon-liner-desktop.svg");
  background-repeat: no-repeat;
  background-position: center;
}
@media (max-width: 1023px) {
  .benefits__item--liner::before {
    left: -11px;
    top: -2px;
    background-image: url("../img/svg/icon-liner.svg");
  }
}
@media (max-width: 767px) {
  .benefits__item--liner::before {
    left: -9px;
    top: -2px;
  }
}

.benefits__item--allinclude::before {
  left: 10px;
  top: 5px;
  background-image: url("../img/svg/icon-allinclude-desktop.svg");
  background-repeat: no-repeat;
  background-position: center;
}
@media (max-width: 1023px) {
  .benefits__item--allinclude::before {
    left: -8px;
    top: -2px;
    background-image: url("../img/svg/icon-allinclude.svg");
  }
}
@media (max-width: 767px) {
  .benefits__item--allinclude::before {
    left: -6px;
    top: -2px;
  }
}

.benefits__item--zodiac::before {
  left: 12px;
  top: 6px;
  background-image: url("../img/svg/icon-tourzodiac-desktop.svg");
  background-repeat: no-repeat;
  background-position: center;
}
@media (max-width: 1023px) {
  .benefits__item--zodiac::before {
    left: -3px;
    top: -2px;
    background-image: url("../img/svg/icon-tourzodiac.svg");
  }
}
@media (max-width: 767px) {
  .benefits__item--zodiac::before {
    left: -1px;
    top: -3px;
  }
}

