}

@media (max-width: 767px) {
  .main-nav__list {
    display: none;
  }
}

@media (max-width: 767px) {
  .main-nav--opened .main-nav__list {
    display: block;
    padding: 40px 0 0 25px;
  }
}

@media (max-width: 767px) {
  .main-nav--opened .main-nav__wrapper-logo {
    display: block;
  }
}

.main-nav__toggle {
  display: none;
}
@media (max-width: 767px) {
  .main-nav__toggle {
    position: absolute;
    top: 9px;
    right: 4px;
    z-index: 11;
    display: block;
    width: 50px;
    height: 50px;
    background: none;
    border: none;
    cursor: pointer;
  }
}

@media (max-width: 767px) {
  .main-nav__toggle::before {
    content: "";
    position: absolute;
    top: 15px;
    left: 15px;
    width: 24px;
    height: 2px;
    background-color: #f9fbfd;
    box-shadow: 0 6px 0 0 #f9fbfd, 0 12px 0 0 #f9fbfd;
  }
}

@media (max-width: 767px) {
  .main-nav__toggle:active::before {
    background-color: rgba(1, 28, 64, 0.3);
    box-shadow: 0 8px 0 0 rgba(1, 28, 64, 0.3), 0 16px 0 0 rgba(1, 28, 64, 0.3);
  }
}

@media (max-width: 767px) {
  .main-nav--no-js .main-nav__toggle {
    display: none;
  }
}

@media (max-width: 767px) {
  .main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
    content: "";
    position: absolute;
    top: 21px;
    left: 22px;
    width: 20px;
    height: 2px;
    background-color: #011c40;
  }
  .main-nav--opened .main-nav__toggle::before {
    box-shadow: none;
    transform: rotate(45deg);
  }
  .main-nav--opened .main-nav__toggle::after {
    transform: rotate(-45deg);
  }
  .main-nav--opened .main-nav__toggle:active::before, .main-nav--opened .main-nav__toggle:active::after {
    background-color: fade(#333333, 30%);
  }
}

.site-list {
