  .site-list {
    column-gap: 23px;
  }
}

@media (max-width: 767px) {
  .site-list__item {
    margin-bottom: 28px;
  }
}

.site-list__link {
  display: block;
  text-decoration: none;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #f9fbfd;
}
.site-list__link:hover {
  opacity: 0.8;
}
.site-list__link:active {
  opacity: 0.6;
}
@media (max-width: 1023px) {
  .site-list__link {
    font-size: 14px;
    line-height: 17px;
  }
}
@media (max-width: 767px) {
