@media (max-width: 767px) {
  .adventure {
    padding: 57px 0 85px 0;
  }
}

.adventure__about h2 {
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
  color: #011c40;
  margin: 58px 0 22px;
}
@media (max-width: 1023px) {
  .adventure__about h2 {
    font-size: 26px;
    line-height: 36px;
    margin: -9px 0 16px -5px;
    width: 210px;
  }
}
@media (max-width: 767px) {
  .adventure__about h2 {
    font-size: 24px;
    line-height: 34px;
    margin: 0 0 15px 0;
    width: 210px;
  }
}

.adventure__about p {
  margin: 0;
}
@media (max-width: 1023px) {
  .adventure__about p {
    font-size: 16px;
    line-height: 24px;
